import { useState, useEffect } from "react";
import styled from "styled-components";
import { gsap } from "gsap";
import { nanoid } from "nanoid";
import { NormalDialog, LongHintBar } from "../components/ChatFrame";
import { NamedWorker, Worker1 } from "../components/Workers";
import { StartButton, GreyButton, DialogBack } from "../components/Buttons";
import { Mark } from "../utils";
import MaskHint from "../components/MaskHint";
import { pageTransition } from "../utils";
import { useNavigate } from "react-router-dom";
import { Stage3DropBox } from "../components/Stage3DropBox";

export default function Stage3() {
  // game data
  const [itemObj, setItemObj] = useState({
    outer: {
      items: [
        {
          content: " Sprint Review",
          id: nanoid(),
          priority: 2,
        },
        {
          content: " Sprint Retrospective",
          id: nanoid(),
          priority: 3,
        },
        {
          content: "(Daily Scrum)",
          id: nanoid(),
          priority: 1,
        },
      ],
    },
    scrum1: {
      item: null,
    },
    scrum2: {
      item: null,
    },
    scrum3: {
      item: null,
    },
  });
  const [isProrityOK, setIsProrityOK] = useState(null);

  // dom state
  const [progress, setProgress] = useState(1);
  const [mask, setMask] = useState(0);
  const [isMask, setIsMask] = useState(false);
  const navigate = useNavigate();

  // run animation and check game state
  useEffect(() => {
    if (progress === 6) {
      gsap
        .timeline()
        .from(".gamebox", {
          yPercent: 100,
          duration: 2,
          ease: "power1",
        })
        .to(".gamebox", {
          clearProps: true,
        })
        .from(".npc1", {
          y: -300,
          duration: 0.5,
          ease: "expo",
        });
    }
  }, [progress]);

  useEffect(() => {
    if (isMask === false && mask === 2)
      pageTransition("body", navigate, "../stage4");
  }, [isMask]);

  const handleStart = () => {
    setProgress(6);
  };
  const handleBack = () => {
    if (progress > 1) setProgress(progress - 1);
  };

  const handleCheck = () => {
    setIsMask(true);
    if (isProrityOK) setMask(2);
    else setMask(1);
  };

  return (
    <StageWrapper className="stage1">
      <>
        {progress === 1 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={1} name={""}></NamedWorker>
          </>
        )}
        {progress === 2 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={1} name={""}></NamedWorker>
          </>
        )}
        {progress === 3 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={1} name={""}></NamedWorker>
          </>
        )}
        {progress === 4 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={1} name={""}></NamedWorker>
          </>
        )}
        {progress === 5 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => handleStart()}>
                <StartButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={1} name={""}></NamedWorker>
          </>
        )}
        {progress > 1 && <DialogBack onClick={() => handleBack()} />}
      </>
      {progress === 6 && (
        <>
          <InlineWorker className="npc1"></InlineWorker>
          <GameWrapper className="gamebox">
            <LongHintBar>
               Scrum 
              
            </LongHintBar>
            <Stage3DropBox
              itemObj={itemObj}
              setItemObj={setItemObj}
              setIsProrityOK={setIsProrityOK}
              handleCheck={handleCheck}
            />
          </GameWrapper>
        </>
      )}
      {isMask &&
        (mask === 1 ? (
          <MaskHint
            name={""}
            number={1}
            content={`
            `}
            btnText={""}
            toggle={setIsMask}
          />
        ) : (
          <MaskHint
            name={""}
            number={1}
            content={`Scrum
            `}
            btnText={""}
            toggle={setIsMask}
          />
        ))}
    </StageWrapper>
  );
}

const InlineWorker = styled(Worker1)`
  position: absolute;
  transform: scale(0.28);
  top: -250px;
  left: 1410px;
  z-index: 99;
`;

const GameWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const StageWrapper = styled.div`
  position: absolute;
  top: 0;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
`;

const text = [
  <>
    {Mark("Sprint")} 
    <br />
     
    <br />
    <br />
  </>,
  <>
    {Mark("Sprint ")}  
    <br />
    {Mark(" Daily Scrum")}
    
    <br />
    <br />
     Sprint 
    <br />
    {Mark(" SprintReview")}
    <br />
    {Mark(" Sprint Retrospective")}
    <br />
    <br />
  </>,
  <>
    {Mark(" Daily Scrum")}
     15 
    <br />
    Sprint Goal
    <br />
    
    <br />
    
    <br />
    <br />
    
    <br />
    <br />
  </>,
  <>
    {Mark(" Sprint Review")}
    <br />
    <br />
    
    <br />
    
    <br />
    <br />
  </>,
  <>
    {Mark(" Sprint Retrospective")}
    <br />
    <br />
    
    <br />
    
    <br />
    
    <br />
    
    <br />
    
    <br />
    <br />
    
    <br />
    Confluence
    <br />
    <br />
  </>,
];
