import { useState, useEffect } from "react";
import styled from "styled-components";
import { gsap } from "gsap";
import { nanoid } from "nanoid";
import { NormalDialog, LongHintBar } from "../components/ChatFrame";
import { NamedWorker, Worker4 } from "../components/Workers";
import { StartButton, GreyButton, DialogBack } from "../components/Buttons";
import { Mark } from "../utils";
import MaskHint from "../components/MaskHint";
import { pageTransition } from "../utils";
import { useNavigate } from "react-router-dom";
import { Stage2DropBox } from "../components/Stage2DropBox";

export default function Stage2() {
  // game data
  const [itemObj, setItemObj] = useState({
    outer: {
      items: [
        {
          content: "",
          id: nanoid(),
          point: "8",
        },
        { content: "", id: nanoid(), point: "13" },
        {
          content: "",
          id: nanoid(),
          point: "4",
        },
        {
          content: "",
          id: nanoid(),
          point: "5",
        },
      ],
    },
    inner: {
      items: [],
    },
  });
  const [isTotalOK, setIsTotalOK] = useState(null);

  // dom state
  const [progress, setProgress] = useState(1);
  const [mask, setMask] = useState(0);
  const [isMask, setIsMask] = useState(false);
  const navigate = useNavigate();

  // run animation and check game state
  useEffect(() => {
    if (progress === 3) {
      gsap.from(".npc div", {
        y: 500,
        duration: 2,
      });
    }
    if (progress === 6) {
      gsap
        .timeline()
        .from(".gamebox", {
          yPercent: 100,
          duration: 2,
          ease: "expo",
        })
        .to(".gamebox", {
          clearProps: true,
        })
        .from(".npc4", {
          y: -300,
          duration: 0.5,
          ease: "expo",
        });
    }
  }, [progress]);

  useEffect(() => {
    if (isMask === false && mask === 2)
      pageTransition("body", navigate, "../stage3");
  }, [isMask]);

  const handleStart = () => {
    setProgress(6);
  };
  const handleBack = () => {
    if (progress > 1) setProgress(progress - 1);
  };

  const handleCheck = () => {
    setIsMask(true);
    if (isTotalOK) setMask(2);
    else setMask(1);
  };

  return (
    <StageWrapper className="stage1">
      <>
        {progress === 1 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={2} name={""}></NamedWorker>
          </>
        )}
        {progress === 2 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={4} name={""}></NamedWorker>
          </>
        )}
        {progress === 3 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={4} name={""}></NamedWorker>
            <div className="npc">
              <NamedWorker
                number={3}
                name={""}
                left={"550px"}
              ></NamedWorker>
              <NamedWorker
                number={1}
                name={""}
                left={"300px"}
                tag={"left"}
              ></NamedWorker>
            </div>
          </>
        )}
        {progress === 4 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => setProgress((state) => state + 1)}>
                <GreyButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={1} name={""}></NamedWorker>
          </>
        )}
        {progress === 5 && (
          <>
            <NormalDialog size="L">
              <div className="text">{text[progress - 1]}</div>
              <div onClick={() => handleStart()}>
                <StartButton content="" />
              </div>
            </NormalDialog>
            <NamedWorker number={3} name={""}></NamedWorker>
          </>
        )}
        {progress > 1 && <DialogBack onClick={() => handleBack()} />}
      </>
      {progress === 6 && (
        <>
          <InlineWorker className="npc4"></InlineWorker>
          <GameWrapper className="gamebox">
            <LongHintBar>
              (20)
            </LongHintBar>
            <Stage2DropBox
              itemObj={itemObj}
              setItemObj={setItemObj}
              setIsTotalOK={setIsTotalOK}
              handleCheck={handleCheck}
            />
          </GameWrapper>
        </>
      )}
      {isMask &&
        (mask === 1 ? (
          <MaskHint
            name={""}
            number={4}
            content={`
            `}
            btnText={""}
            toggle={setIsMask}
          />
        ) : (
          <MaskHint
            name={""}
            number={4}
            content={""}
            btnText={""}
            toggle={setIsMask}
          />
        ))}
    </StageWrapper>
  );
}

const InlineWorker = styled(Worker4)`
  position: absolute;
  transform: scale(0.28);
  top: -220px;
  left: 1420px;
`;

const GameWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const StageWrapper = styled.div`
  position: absolute;
  top: 0;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
`;

const text = [
  <>
    
    <br />
     Scrum Master 
    <br />
    {Mark("Sprint Planning")}<br />
    
    <br />
    <br />
    {Mark("Sprint Backlog")}
    
    <br />
    <br />
  </>,
  <>
    
    <br />
     Scrum Master 
    <br />
    
    Scrum 
    <br />
    <br />
  </>,
  <>
      Sprint
     (Sprint Point) 
    20 
    <br />
    <br />
  </>,
  <>
    
    <br />
    
    <br />
    Sprint Point 
    <br />
    <br />
  </>,
  <>
    
    <br />
    By the way
    <a
      href="https://www.atlassian.com/software/jira"
      style={{ verticalAlign: "middle" }}
    >
      <img
        src="./images/jira_logo.png"
        alt="Jira"
        style={{ margin: "0 7px" }}
      />
    </a>
    
    <br />
    <br />
  </>,
];
